import React, { useState, useEffect } from "react";
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";
import Icon from "@mui/material/Icon";
import Chip from "@mui/material/Chip";
import LinearProgress from "@mui/material/LinearProgress";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
import Alert from "@mui/material/Alert";
import Snackbar from "@mui/material/Snackbar";
import Tooltip from "@mui/material/Tooltip";
import IconButton from "@mui/material/IconButton";

import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDButton from "components/MDButton";
import { VhoozhtInput, VhoozhtSelect, VhoozhtTextarea } from "components/VhoozhtForms";
import { formatters } from "utils/dataFormatters";

import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import Footer from "examples/Footer";
import ComplexStatisticsCard from "examples/Cards/StatisticsCards/ComplexStatisticsCard";
import ReportsBarChart from "examples/Charts/BarCharts/ReportsBarChart";
import ReportsLineChart from "examples/Charts/LineCharts/ReportsLineChart";
import PieChart from "examples/Charts/PieChart";

// Import existing enhanced components
import AdminUsersEnhanced from "./usersEnhanced";
import AdminLoansEnhanced from "./loansEnhanced";
import AdminPaymentsEnhanced from "./paymentsEnhanced";
import AdminSettings from "./settings";

import adminDataService from "../../services/adminDataService";
// import glowing icons CSS
import "../../assets/css/glowing-icons.css";

// Enhanced Admin Statistics Card that matches user dashboard styling
function AdminStatisticsCard({ title, value, change, icon, color, subtitle, trend = [], loading = false }) {
  // Calculate percentage change for ComplexStatisticsCard format
  const changePercent = change || "+0%";
  const changeColor = changePercent.startsWith('+') ? "success" : changePercent.startsWith('-') ? "error" : "info";
  
  return (
    <MDBox mb={1.5}>
      <ComplexStatisticsCard
        color={color === "#2196f3" ? "info" : 
              color === "#4caf50" ? "success" : 
              color === "#ff9800" ? "warning" :
              color === "#9c27b0" ? "primary" : "dark"}
        icon={icon}
        title={title}
        count={loading ? "..." : value}
        percentage={{
          color: changeColor,
          amount: changePercent,
          label: subtitle || "since last period",
        }}
      />
    </MDBox>
  );
}

// Document Verification Component
function DocumentVerificationPanel() {
  const [documents, setDocuments] = useState([
    {
      id: 1,
      userName: "John Doe",
      userId: "USR001",
      documentName: "Driver's License",
      category: "Identity Documents",
      uploadDate: "2025-09-01T14:30:00",
      status: "pending",
      size: "2.4 MB",
      type: "PDF",
      loanId: "LN001"
    },
    {
      id: 2,
      userName: "Jane Smith",
      userId: "USR002",
      documentName: "Bank Statement",
      category: "Financial Documents",
      uploadDate: "2025-09-01T16:45:00",
      status: "pending",
      size: "1.8 MB",
      type: "PDF",
      loanId: "LN002"
    },
    {
      id: 3,
      userName: "Mike Johnson",
      userId: "USR003",
      documentName: "Employment Letter",
      category: "Income Verification",
      uploadDate: "2025-09-02T09:15:00",
      status: "pending",
      size: "1.2 MB",
      type: "PDF",
      loanId: "LN003"
    }
  ]);

  const [selectedDocument, setSelectedDocument] = useState(null);
  const [verificationDialog, setVerificationDialog] = useState(false);
  const [verificationData, setVerificationData] = useState({
    status: '',
    notes: ''
  });
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });

  const handleVerifyDocument = (document) => {
    setSelectedDocument(document);
    setVerificationData({ status: '', notes: '' });
    setVerificationDialog(true);
  };

  const handleSubmitVerification = () => {
    if (!verificationData.status) {
      setSnackbar({ open: true, message: 'Please select a verification status', severity: 'error' });
      return;
    }

    // Update document status
    setDocuments(prev => prev.map(doc => 
      doc.id === selectedDocument.id 
        ? { 
            ...doc, 
            status: verificationData.status,
            verificationDate: new Date().toISOString(),
            adminNotes: verificationData.notes,
            verifiedBy: 'Admin'
          }
        : doc
    ));

    setSnackbar({ 
      open: true, 
      message: `Document ${verificationData.status} successfully`, 
      severity: verificationData.status === 'approved' ? 'success' : 'warning'
    });

    setVerificationDialog(false);
  };

  const formatDate = (date) => formatters.datetime(date);

  const pendingDocuments = documents.filter(doc => doc.status === 'pending');

  return (
    <MDBox>
      <Card sx={{ p: 3 }}>
        <MDBox display="flex" justifyContent="space-between" alignItems="center" mb={3}>
          <MDBox>
            <MDTypography variant="h5" fontWeight="bold">
              Document Verification Queue
            </MDTypography>
            <MDTypography variant="body2" color="text" opacity={0.7}>
              {pendingDocuments.length} documents waiting for verification
            </MDTypography>
          </MDBox>
          <Chip 
            label={`${pendingDocuments.length} Pending`}
            color="warning"
            sx={{ fontWeight: 'bold' }}
          />
        </MDBox>

        {pendingDocuments.length === 0 ? (
          <MDBox textAlign="center" py={6}>
            <Icon sx={{ fontSize: 64, color: '#9e9e9e', mb: 2 }}>verified</Icon>
            <MDTypography variant="h6" fontWeight="bold" mb={1}>
              All Caught Up!
            </MDTypography>
            <MDTypography variant="body2" color="text" opacity={0.7}>
              No documents pending verification at this time.
            </MDTypography>
          </MDBox>
        ) : (
          <TableContainer component={Paper} sx={{ boxShadow: 'none' }}>
            <Table sx={{ '& td, & th': { verticalAlign: 'middle' } }}>
              <TableHead>
                <TableRow>
                  <TableCell><MDTypography variant="caption" fontWeight="bold">User</MDTypography></TableCell>
                  <TableCell><MDTypography variant="caption" fontWeight="bold">Document</MDTypography></TableCell>
                  <TableCell><MDTypography variant="caption" fontWeight="bold">Category</MDTypography></TableCell>
                  <TableCell><MDTypography variant="caption" fontWeight="bold">Upload Date</MDTypography></TableCell>
                  <TableCell><MDTypography variant="caption" fontWeight="bold">Size</MDTypography></TableCell>
                  <TableCell><MDTypography variant="caption" fontWeight="bold">Actions</MDTypography></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {pendingDocuments.map((document) => (
                  <TableRow key={document.id}>
                    <TableCell>
                      <MDBox display="flex" alignItems="center">
                        <Avatar sx={{ bgcolor: '#2196f3', mr: 2, width: 32, height: 32 }}>
                          <MDTypography variant="caption" fontWeight="bold" sx={{ color: 'white' }}>
                            {document.userName.split(' ').map(n => n[0]).join('')}
                          </MDTypography>
                        </Avatar>
                        <MDBox>
                          <MDTypography variant="body2" fontWeight="medium">
                            {document.userName}
                          </MDTypography>
                          <MDTypography variant="caption" color="text" opacity={0.7}>
                            ID: {document.userId}
                          </MDTypography>
                        </MDBox>
                      </MDBox>
                    </TableCell>
                    <TableCell>
                      <MDBox>
                        <MDTypography variant="body2" fontWeight="medium">
                          {document.documentName}
                        </MDTypography>
                        <MDTypography variant="caption" color="text" opacity={0.7}>
                          {document.type}  Loan: {document.loanId}
                        </MDTypography>
                      </MDBox>
                    </TableCell>
                    <TableCell>
                      <MDTypography variant="body2">
                        {document.category}
                      </MDTypography>
                    </TableCell>
                    <TableCell>
                      <MDTypography variant="body2">
                        {formatDate(document.uploadDate)}
                      </MDTypography>
                    </TableCell>
                    <TableCell>
                      <MDTypography variant="body2">
                        {document.size}
                      </MDTypography>
                    </TableCell>
                    <TableCell>
                      <MDBox display="flex" gap={1}>
                        <Tooltip title="View Document">
                          <IconButton size="small" sx={{ color: '#2196f3' }}>
                            <Icon fontSize="small">visibility</Icon>
                          </IconButton>
                        </Tooltip>
                        <Tooltip title="Download">
                          <IconButton size="small" sx={{ color: '#4caf50' }}>
                            <Icon fontSize="small">download</Icon>
                          </IconButton>
                        </Tooltip>
                        <Tooltip title="Verify Document">
                          <IconButton 
                            size="small" 
                            sx={{ color: '#ff9800' }}
                            onClick={() => handleVerifyDocument(document)}
                          >
                            <Icon fontSize="small">verified_user</Icon>
                          </IconButton>
                        </Tooltip>
                      </MDBox>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        )}
      </Card>

      {/* Verification Dialog */}
      <Dialog open={verificationDialog} onClose={() => setVerificationDialog(false)} maxWidth="sm" fullWidth>
        <DialogTitle>
          <MDBox display="flex" alignItems="center">
            <Avatar sx={{ bgcolor: '#ff9800', mr: 2 }}>
              <Icon>verified_user</Icon>
            </Avatar>
            <MDBox>
              <MDTypography variant="h5" fontWeight="bold">
                Document Verification
              </MDTypography>
              <MDTypography variant="caption" color="text">
                {selectedDocument?.documentName} - {selectedDocument?.userName}
              </MDTypography>
            </MDBox>
          </MDBox>
        </DialogTitle>
        
        <DialogContent>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <VhoozhtSelect
                fullWidth
                label="Verification Status"
                value={verificationData.status}
                onChange={(e) => setVerificationData(prev => ({ ...prev, status: e.target.value }))}
                options={[
                  {
                    value: "approved",
                    label: (
                      <MDBox display="flex" alignItems="center">
                        <Icon sx={{ color: '#4caf50', mr: 1 }}>check_circle</Icon>
                        Approved
                      </MDBox>
                    )
                  },
                  {
                    value: "rejected",
                    label: (
                      <MDBox display="flex" alignItems="center">
                        <Icon sx={{ color: '#f44336', mr: 1 }}>cancel</Icon>
                        Rejected
                      </MDBox>
                    )
                  },
                  {
                    value: "requires_action",
                    label: (
                      <MDBox display="flex" alignItems="center">
                        <Icon sx={{ color: '#ff9800', mr: 1 }}>warning</Icon>
                        Requires Action
                      </MDBox>
                    )
                  }
                ]}
                size="medium"
              />
            </Grid>
            
            <Grid item xs={12}>
              <VhoozhtTextarea
                fullWidth
                label="Admin Notes"
                rows={4}
                value={verificationData.notes}
                onChange={(e) => setVerificationData(prev => ({ ...prev, notes: e.target.value }))}
                placeholder="Add notes for the user about this verification decision..."
                size="medium"
              />
            </Grid>
          </Grid>
        </DialogContent>
        
        <DialogActions>
          <MDButton variant="outlined" color="secondary" onClick={() => setVerificationDialog(false)}>
            Cancel
          </MDButton>
          <MDButton 
            variant="gradient" 
            color={verificationData.status === 'approved' ? 'success' : verificationData.status === 'rejected' ? 'error' : 'warning'}
            onClick={handleSubmitVerification}
            startIcon={<Icon>verified_user</Icon>}
          >
            Submit Verification
          </MDButton>
        </DialogActions>
      </Dialog>

      {/* Snackbar */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={() => setSnackbar(prev => ({ ...prev, open: false }))}
      >
        <Alert severity={snackbar.severity} onClose={() => setSnackbar(prev => ({ ...prev, open: false }))}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </MDBox>
  );
}

// Main Admin Dashboard Component
function UnifiedAdminDashboard() {
  const [activeTab, setActiveTab] = useState(0);
  const [metrics, setMetrics] = useState({});
  const [loading, setLoading] = useState(true);

  // Admin dashboard chart data
  const adminChartsData = {
    // User growth chart data
    userGrowthChart: {
      labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep"],
      datasets: { 
        label: "User Growth",
        data: [120, 135, 128, 156, 148, 167, 178, 189, 203]
      }
    },
    // Loan approval trends
    loanTrendsChart: {
      labels: ["Week 1", "Week 2", "Week 3", "Week 4"],
      datasets: [
        {
          label: "Approved",
          data: [28, 32, 35, 38],
          color: "success"
        },
        {
          label: "Pending", 
          data: [12, 18, 15, 22],
          color: "warning"
        },
        {
          label: "Rejected",
          data: [3, 5, 4, 6],
          color: "error"
        }
      ]
    },
    // Revenue distribution pie chart
    revenueChart: {
      labels: ["Loan Interest", "Processing Fees", "Late Fees", "Other"],
      datasets: [{
        data: [65, 25, 8, 2],
        backgroundColor: ['#4caf50', '#2196f3', '#ff9800', '#9c27b0']
      }]
    }
  };

  useEffect(() => {
    const loadMetrics = async () => {
      setLoading(true);
      try {
        // Get data from admin service
        const users = adminDataService.getUsers();
        const loans = adminDataService.getLoans();
        const payments = adminDataService.getPayments();
        
        // Calculate enhanced metrics with trends
        const totalUsers = users.total;
        const activeUsers = users.items.filter(u => u.status === 'active').length;
        const newUsersThisMonth = users.items.filter(u => {
          const userDate = new Date(u.createdDate);
          const now = new Date();
          return userDate.getMonth() === now.getMonth() && userDate.getFullYear() === now.getFullYear();
        }).length;
        
        const totalLoans = loans.total;
        const pendingLoans = loans.items.filter(l => l.status === 'pending').length;
        const approvedLoans = loans.items.filter(l => l.status === 'approved').length;
        const totalLoanAmount = loans.items.reduce((sum, loan) => sum + loan.amount, 0);
        
        const totalRevenue = payments.items.reduce((sum, payment) => sum + payment.amount, 0);
        const monthlyRevenue = payments.items
          .filter(p => {
            const paymentDate = new Date(p.date);
            const now = new Date();
            return paymentDate.getMonth() === now.getMonth() && paymentDate.getFullYear() === now.getFullYear();
          })
          .reduce((sum, payment) => sum + payment.amount, 0);

        // Mock trend data for the last 12 periods
        const userTrend = [120, 135, 128, 156, 148, 167, 178, 189, 195, 203, 218, activeUsers];
        const loanTrend = [45, 52, 48, 61, 55, 67, 73, 78, 82, 89, 95, totalLoans];
        const revenueTrend = [8500, 9200, 8900, 10500, 9800, 11200, 12100, 12800, 13400, 14200, 15100, monthlyRevenue];

        setMetrics({
          totalUsers,
          activeUsers,
          newUsersThisMonth,
          totalLoans,
          pendingLoans,
          approvedLoans,
          totalLoanAmount,
          totalRevenue,
          monthlyRevenue,
          userTrend,
          loanTrend,
          revenueTrend
        });
      } catch (error) {
        console.error('Error loading metrics:', error);
      } finally {
        setLoading(false);
      }
    };

    loadMetrics();
  }, []);

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(amount);
  };

  // Admin Dashboard Overview Tab (Enhanced to match user dashboard)
  const DashboardOverview = () => (
    <MDBox>
      {/* Admin Statistics Cards - Matching User Dashboard Style */}
      <Grid container spacing={3} mb={4}>
        <Grid item xs={12} md={6} lg={3}>
          <div className="user-management-card enhanced-stats-card">
            <AdminStatisticsCard
              title="Total Users"
              value={metrics.totalUsers?.toString() || "0"}
              change="+12.5%"
              icon={<Icon className="glow-icon-user glow-icon-active" sx={{ fontSize: 28 }}>people</Icon>}
              color="#2196f3"
              subtitle={`${metrics.activeUsers || 0} active  ${metrics.newUsersThisMonth || 0} new this month`}
              trend={metrics.userTrend || []}
              loading={loading}
            />
          </div>
        </Grid>
        
        <Grid item xs={12} md={6} lg={3}>
          <div className="loan-management-card enhanced-stats-card">
            <AdminStatisticsCard
              title="Active Loans"
              value={metrics.totalLoans?.toString() || "0"}
              change="+8.2%"
              icon={<Icon className="glow-icon-loan glow-icon-success" sx={{ fontSize: 28 }}>account_balance</Icon>}
              color="#4caf50"
              subtitle={`${metrics.pendingLoans || 0} pending  ${formatCurrency(metrics.totalLoanAmount || 0)} disbursed`}
              trend={metrics.loanTrend || []}
              loading={loading}
            />
          </div>
        </Grid>
        
        <Grid item xs={12} md={6} lg={3}>
          <div className="payment-management-card enhanced-stats-card">
            <AdminStatisticsCard
              title="Monthly Revenue"
              value={formatCurrency(metrics.monthlyRevenue || 0)}
              change="+15.3%"
              icon={<Icon className="glow-icon-payment glow-icon-success" sx={{ fontSize: 28 }}>payments</Icon>}
              color="#9c27b0"
              subtitle={`${formatCurrency(metrics.totalRevenue || 0)} total revenue`}
              trend={metrics.revenueTrend || []}
              loading={loading}
            />
          </div>
        </Grid>
        
        <Grid item xs={12} md={6} lg={3}>
          <AdminStatisticsCard
            title="Approval Rate"
            value="94.2%"
            change="+2.1%"
            icon={<Icon sx={{ color: '#9c27b0', fontSize: 28 }}>trending_up</Icon>}
            color="primary"
            subtitle="loan approvals this month"
            trend={[89, 90, 91, 92, 93, 93, 94, 94]}
            loading={loading}
          />
        </Grid>
      </Grid>

      {/* Admin Analytics Charts - Similar to User Dashboard */}
      <Grid container spacing={3} mb={4}>
        <Grid item xs={12} md={6} lg={4}>
          <MDBox mb={3}>
            <ReportsBarChart
              color="info"
              title="User Growth Analytics"
              description="Monthly user registration trends"
              date="Updated daily"
              chart={adminChartsData.userGrowthChart}
            />
          </MDBox>
        </Grid>
        
        <Grid item xs={12} md={6} lg={4}>
          <MDBox mb={3}>
            <PieChart
              color="success"
              title="Revenue Distribution"
              description="Revenue sources breakdown"
              date="Updated this month"
              chart={adminChartsData.revenueChart}
            />
          </MDBox>
        </Grid>
        
        <Grid item xs={12} md={6} lg={4}>
          <MDBox mb={3}>
            <ReportsLineChart
              color="warning"
              title="Loan Processing Trends"
              description="Weekly loan approval patterns"
              date="Updated weekly"
              chart={{
                labels: ["W1", "W2", "W3", "W4"],
                datasets: {
                  label: "Processed Loans",
                  data: [43, 55, 54, 66]
                }
              }}
            />
          </MDBox>
        </Grid>
      </Grid>

      {/* Document Verification Panel */}
      <Grid container spacing={3} mb={4}>
        <Grid item xs={12}>
          <DocumentVerificationPanel />
        </Grid>
      </Grid>

      {/* Bottom Section - Activity Overview & Admin Tools */}
      <Grid container spacing={3}>
        <Grid item xs={12} md={6} lg={8}>
          <Card sx={{ p: 3, height: '400px' }}>
            <MDBox display="flex" justifyContent="space-between" alignItems="center" mb={3}>
              <MDBox>
                <MDTypography variant="h6" fontWeight="bold">
                  Recent System Activity
                </MDTypography>
                <MDTypography variant="body2" color="text" opacity={0.7}>
                  Live admin activity feed
                </MDTypography>
              </MDBox>
              <MDButton variant="outlined" color="info" size="small">
                View All Activity
              </MDButton>
            </MDBox>
            
            <MDBox sx={{ height: '300px', overflowY: 'auto' }}>
              {[
                { type: 'user', title: 'New user registered: Sarah Wilson', time: '5 minutes ago', color: '#2196f3' },
                { type: 'loan', title: 'Loan application submitted: $15,000', time: '12 minutes ago', color: '#ff9800' },
                { type: 'document', title: 'Document uploaded for verification', time: '18 minutes ago', color: '#9c27b0' },
                { type: 'payment', title: 'Payment processed: $850', time: '25 minutes ago', color: '#4caf50' },
                { type: 'loan', title: 'Loan approved: $25,000', time: '32 minutes ago', color: '#4caf50' },
                { type: 'document', title: 'Document verification completed', time: '45 minutes ago', color: '#9c27b0' },
                { type: 'user', title: 'User profile updated: John Smith', time: '1 hour ago', color: '#2196f3' },
              ].map((activity, index) => (
                <MDBox
                  key={index}
                  display="flex"
                  alignItems="center"
                  py={2}
                  borderBottom={index < 6 ? "1px solid #f0f0f0" : "none"}
                >
                  <Avatar sx={{ bgcolor: activity.color, mr: 2, width: 40, height: 40 }}>
                    <Icon fontSize="small" sx={{ color: 'white' }}>
                      {activity.type === 'user' ? 'person' : 
                       activity.type === 'loan' ? 'account_balance' : 
                       activity.type === 'document' ? 'folder' : 'payment'}
                    </Icon>
                  </Avatar>
                  <MDBox flex={1}>
                    <MDTypography variant="body2" fontWeight="medium">
                      {activity.title}
                    </MDTypography>
                    <MDTypography variant="caption" color="text" opacity={0.7}>
                      {activity.time}
                    </MDTypography>
                  </MDBox>
                </MDBox>
              ))}
            </MDBox>
          </Card>
        </Grid>
        
        <Grid item xs={12} md={6} lg={4}>
          <Card sx={{ p: 3, height: '400px' }}>
            <MDBox mb={3}>
              <MDTypography variant="h6" fontWeight="bold" mb={1}>
                Admin Quick Actions
              </MDTypography>
              <MDTypography variant="body2" color="text" opacity={0.7}>
                Frequently used admin tools
              </MDTypography>
            </MDBox>
            
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <MDButton
                  variant="gradient"
                  color="info"
                  fullWidth
                  startIcon={<Icon>person_add</Icon>}
                  sx={{ justifyContent: 'flex-start', py: 2 }}
                >
                  Add New User
                </MDButton>
              </Grid>
              
              <Grid item xs={12}>
                <MDButton
                  variant="gradient"
                  color="warning"
                  fullWidth
                  startIcon={<Icon>pending_actions</Icon>}
                  sx={{ justifyContent: 'flex-start', py: 2 }}
                  onClick={() => setActiveTab(2)}
                >
                  Review Pending Loans
                </MDButton>
              </Grid>
              
              <Grid item xs={12}>
                <MDButton
                  variant="gradient"
                  color="success"
                  fullWidth
                  startIcon={<Icon>verified_user</Icon>}
                  sx={{ justifyContent: 'flex-start', py: 2 }}
                >
                  Verify Documents
                </MDButton>
              </Grid>
              
              <Grid item xs={12}>
                <MDButton
                  variant="gradient"
                  color="error"
                  fullWidth
                  startIcon={<Icon>assessment</Icon>}
                  sx={{ justifyContent: 'flex-start', py: 2 }}
                >
                  Generate Reports
                </MDButton>
              </Grid>
            </Grid>

            {/* System Health Monitor removed per request */}
          </Card>
        </Grid>
      </Grid>
    </MDBox>
  );

  return (
    <DashboardLayout>
      <DashboardNavbar />
      <MDBox py={3}>
        {/* Enhanced Admin Header - Matching User Dashboard */}
        <MDBox mb={4}>
          <Grid container spacing={3} alignItems="center">
            <Grid item xs={12} lg={8}>
              <MDTypography variant="h4" fontWeight="bold" color="text" mb={1}>
                Admin Dashboard 
              </MDTypography>
              <MDTypography variant="body1" color="text" opacity={0.7}>
                Comprehensive platform administration with real-time analytics and user management capabilities.
              </MDTypography>
            </Grid>
            <Grid item xs={12} lg={4}>
              <MDBox display="flex" justifyContent={{ xs: "flex-start", lg: "flex-end" }} gap={2}>
                <MDButton
                  variant="gradient"
                  color="info"
                  startIcon={<Icon>add</Icon>}
                  size="small"
                >
                  Add User
                </MDButton>
                <MDButton
                  variant="outlined"
                  color="dark"
                  startIcon={<Icon>download</Icon>}
                  size="small"
                >
                  Export Data
                </MDButton>
              </MDBox>
            </Grid>
          </Grid>
        </MDBox>

        {/* Tabs removed - navigation handled via sidebar/topbar */}
        
        {activeTab === 0 && <DashboardOverview />}
        {activeTab === 1 && <AdminUsersEnhanced />}
        {activeTab === 2 && <AdminLoansEnhanced />}
        {activeTab === 3 && <AdminPaymentsEnhanced />}
        {activeTab === 4 && <AdminSettings />}
      </MDBox>
      <Footer />
    </DashboardLayout>
  );
}

export default UnifiedAdminDashboard;
